<template>
  <div>
    <div v-if="loading" class="center">
      <loading />
    </div>
    <div v-if="solutionModel" class="container">
      <exam
        :exam="exam"
        :solution_model="solutionModel"
        :isSolutionModel="true"
        :solutionModelId="solutionModelId"
      />
    </div>
  </div>
</template>

<script>
import Exam from "../exam-component.vue";
import axios from "axios";
import { generateExamModel } from "../../functions";
export default {
  components: {
    Exam,
  },
  created() {
    const params = this.$route.params;
    if (params.examId) this.getExamForAdmin(params.examId);
  },
  data() {
    return {
      loading: false,
      exam: null,
      solutionModel: null,
      solutionModelId: null,
    };
  },
  methods: {
    async getExamForAdmin(examId) {
      const res = await axios.get("/fetchUnitExamById/" + examId);
      const exam = { ...res.data.exam };
      this.exam = exam;
      this.loading = true;
      axios.get("/fetchUnitExamSolutionModelForAdmin/" + examId).then((res) => {
        this.loading = false;
        if (res.data.solution) this.solutionModelId = res.data.solution._id;
        this.solutionModel = { examId: exam._id };
        if (res.data.solution)
          this.solutionModel["sections"] = res.data.solution.sections;
        else this.solutionModel["sections"] = generateExamModel(exam);
      });
    },
  },
};
</script>
